.game-tile {
    margin: 15px;
  }
  
  .game-tile .ant-card {
    width: 100%;
    border-radius: 10px;
    transition: all 0.3s ease;
  }
  
  .game-tile .ant-card:hover {
    transform: scale(1.05);
  }
  
  .game-tile .ant-card-cover img {
    border-radius: 10px;
  }
  
  
  